import axios from '@/axios'
import qs from 'qs'

const article = {
  // 
  articleList () {
    return axios.get(`/topics`)
  },
  // ,
  articleDetail (id, params) {
    return axios.get(`/topic/${id}`, {
      params: params
    })
  },
  // post
  login (params) {
    return axios.post(`/accesstoken`, qs.stringify(params))
  }
}

export default article
